.Trabajos{
    position: relative;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}
.misTrabajos, .masTrabajo{
    backface-visibility: hidden;
    transition: transform 1s;
}
.masTrabajo{
    position: absolute;
    top:0px;
    left: 0px;
    width: 100%;
    color: #fff;
}
.masTrabajo p{
    border: solid #fff 3px;
    width: 95%;
    border-radius: 10px;
    padding: 10px;
    font-weight: 800;
}
.masTrabajo h3{
    font-size: 30px;
    text-align: center;
}
.masTrabajo a, .masTrabajo a:hover, .masTrabajo a:visited, .Atras{
    display: block;
    text-align: center;
    width: 45%;
    font-size: 25px;
    color: #000;
    text-decoration: none;
    background-color: #fff;
    font-weight: 900;
    padding: 5px;
    border-radius: 10px;
    margin: 0px 1%;
    float: left;
    cursor: pointer;
}
.masTrabajo img{
    width: 98%;
    border-radius: 10px;
}
.Trabajo{
    width: 33%;
    border-radius: 5px;
    overflow: hidden;
}
.Trabajo img{
    width: 100%;
    margin:0px
}
.Trabajo h3{
    background-color: #fff;
    margin: 0px;
    text-align: center;
}
.Rotar{
    transform: rotateY(180deg)
}