.Visor{
    background-color: #fff2;
    overflow-y: hidden;
    padding: 15px;
    position: absolute;
    height: 80%;
    width: 68%;
    right: 3%;
    top: 10%;
    transition: transform 1s;  
}

.sombra{
    box-shadow: #000 3px 3px 5px 1px;
}

.Arriba{
    animation: arriba 1s ;
}

@keyframes arriba{
    50%{
        transform:  rotateZ(90deg);
        top:-100%;
        right:-70%
    }
    51%{
        transform:  rotateZ(-90deg);
        top:200%;
        right:-70%
    }
}

.Abajo{
    animation: abajo 1s ;
}

@keyframes abajo{
    50%{
        transform:  rotateZ(-90deg);
        top:200%;
        right:-70%
    }
    51%{
        transform:  rotateZ(90deg);
        top:-100%;
        right:-70%
    }
}