@font-face{
  font-family:'Robota';
  src: url('./Fuente/Robota.otf');
}
body{
  background-image: url('fondo.svg');
  background-size: cover;
  background-repeat: no-repeat;
}
.App{
  position: relative;
  overflow:hidden;
}
h1{
  color:#fff;
}
*::-webkit-scrollbar{
  width: 9px;
  box-sizing: border-box;
}
*::-webkit-scrollbar-thumb{
  background-color: #fff9;
  border-radius: 5px;
}
#miNombre{
  position: absolute;
  top:20px;
  left:4%;
  font-family: Robota;
  font-size: 1.8rem;
  font-weight: 100;
}

