.boton{
    color:black;
    background-color: #fff;
    text-align: center;
    margin-bottom: 30px;
    box-shadow: #333 3px 3px 5px;
    padding: 10px;
    border-radius: 5px;
    width: 93%;
    transition: transform 1.5s;
    position: relative;
}

.botonAction{
    padding:5px;
}

.botonAction:hover > .activo, .activo{
    transition: transform 0.5s;
    transform: translateX(15px) !important;
}

.botonAction:hover > .boton{
    transform: rotateY(360deg) translateZ(200px);
}

